.btn-nav { position: relative; display: none; vertical-align: middle; width: 40px; height: 30px; background-color: transparent; color: #dfdfe2; z-index: 6000; }
.btn-nav:before, .btn-nav:after, .btn-nav span { position: absolute; content: ""; top: 13px; left: 0; right: 0; height: 4px; border-radius: 20px; background-color: #a70c5d; transition: transform 0.3s; }
.btn-nav:before { top: 0; }
