.is-fixed { position: fixed; top: 0; background-color: #ffffff; border: 1px solid rgba(0, 0, 0, 0.06); box-shadow: 0 0 1px 2px rgba(0, 0, 0, 0.05); }

.is-clicked .btn-nav:before { transform: translateY(13px) rotate(45deg); }
