.contacts-caption { padding-bottom: 11px; }

.order { text-align: center; }
.order-col { display: inline-block; vertical-align: top; margin: 0 20px; max-width: 380px; min-width: 300px; text-align: left; }
@media only screen and (max-width: 560px) { .order-col { margin: 0; padding: 0 10px; } }
.order-input { display: inline-block; width: 100%; padding: 29px 0 8px 0; border-bottom: 1px solid #dcdcdc; font-family: "gp-black", sans-serif; font-size: 11px; text-transform: uppercase; }
@media only screen and (max-width: 360px) { .order-input { text-align: center; font-size: 14px; } }
.order-input::placeholder { display: inline-block; font-family: "gp-medium", sans-serif; font-size: 11px; text-transform: uppercase; line-height: 1; color: #3f4147; }
.order-subject { width: 100%; }
.order-btn-theme { width: 100%; margin-top: 49px; padding: 0 20px; position: relative; text-align: left; background-color: #fcfcfd; border: 1px solid #dbdbdb; font-family: "gp-medium", sans-serif; font-size: 11px; text-transform: uppercase; line-height: 50px; color: #3f4147; transition: all 0.2s; }
.order-btn-theme .icon-arr { position: absolute; right: 15px; top: 50%; transform: translateY(-50%) rotate(0deg); width: 22px; text-align: center; font-size: 22px; color: #c9cacf; transition: all 0.2s; }
.order-btn-theme:hover { color: #a70c5d; }
@media only screen and (max-width: 360px) { .order-btn-theme { font-size: 14px; } }
.order-list { display: none; margin-top: -1px; padding: 20px 23px; border: 1px solid #dbdbdb; }
.order-service { width: 100%; padding: 5px 0; }
.order-option { display: none; }
.order-option + label { cursor: pointer; }
.order-option:not(checked) + label { font-family: "gp-regular", sans-serif; font-size: 13px; line-height: 1.6; color: #3f4147; }
.order-option:not(checked) + label .icon-list { color: #c9cacf; font-size: 7px; vertical-align: middle; }
@media only screen and (max-width: 560px) { .order-option:not(checked) + label { font-size: 16px; line-height: 2; }
  .order-option:not(checked) + label .icon-list { font-size: 10px; } }
.order-option:checked + label { font-family: "gp-bold", sans-serif; color: #a70c5d; }
.order-option:checked + label .icon-list { color: #a70c5d; }
.order-choice .icon-list { padding-right: 11px; }
.order-choice:hover { color: #a70c5d; }
.order-choice:hover .icon-list { color: #a70c5d; }
.order-txt { display: inline-block; width: 100%; padding-top: 23px; font-family: "gp-medium", sans-serif; font-size: 11px; text-transform: uppercase; line-height: 25px; color: #3f4147; }
@media only screen and (max-width: 360px) { .order-txt { padding-top: 50px; text-align: center; font-size: 14px; } }
.order-msg { width: 100%; height: 250px; padding: 14px; outline: none; resize: none; border: 1px solid #dcdcdc; font-family: "gp-bold", sans-serif; font-size: 15px; line-height: 1.333; color: #3f4147; }
@media only screen and (max-width: 360px) { .order-msg { font-size: 18px; } }
.order-msg::placeholder { font-family: "gp-regular", sans-serif; font-size: 15px; line-height: 1.333; color: #3f4147; opacity: 0.5; }
.order-send { width: 100%; padding: 36px 0 31px 0; }
.order-btn { width: 180px; line-height: 50px; }

.is-show .icon-arr { transform: translateY(-50%) rotate(180deg); }

.error-box { padding: 5px 0; font-family: "gp-regular", sans-serif; font-size: 12px; line-height: 1.2; width: 100%; }

/* END  contacts */
/* BEGIN  footer */
.footer { width: 100%; padding: 30px 0; }
.footer-in { width: 320px; margin: auto; text-align: center; }
@media only screen and (max-width: 360px) { .footer-in { padding: 10px; } }
.footer-txt { display: inline-block; width: 100%; }
.footer-company { padding-bottom: 21px; font-family: "gp-black", sans-serif; font-size: 11px; text-transform: uppercase; color: #3f4147; }
.footer-address { font-family: "gp-regular", sans-serif; font-size: 18px; color: #3f4147; }
@media only screen and (max-width: 360px) { .footer-address { line-height: 1.8; } }
.footer-link { display: inline-block; padding-top: 10px; font-family: "gp-regular", sans-serif; font-size: 18px; line-height: 1.2; color: #3f4147; border-bottom: 1px solid transparent; transition: all 0.2s; }
.footer-link:hover { color: #a70c5d; border-bottom: 1px solid #a70c5d; }
.footer-soc { display: inline-block; width: 40px; height: 40px; margin: 23px 2px; border-radius: 50%; border: 2px solid #dcdcdc; color: #dcdcdc; line-height: 36px; transition: all 0.2s; }
.footer-soc:hover { color: #4a6bb8; border-color: #4a6bb8; }
.footer-copy { font-family: "gp-regular", sans-serif; font-size: 12px; color: #3f4147; }

.icon-fb { font-size: 12px; }

.icon-tw { font-size: 11px; }

.icon-in { font-size: 13px; }

/* END  footer */
